.slider {
  width: 100%;
  height: 100%;
  position: relative;
}
.slider .slider__nav {
  position: absolute;
  height: 100%;
  right: 160px;
  top: 0;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
  font-size: 20px;
  color: white;
}
.slider .slider__nav-point {
  width: 8px;
  height: 8px;
  box-sizing: border-box;
  border: solid white 2px;
  margin: 16px 0;
}
.slider .slider__nav-point_current {
  background: white;
}
.slider__panel {
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
}
.slider__panel .slider__part {
  width: 50%;
  height: 50%;
  overflow: hidden;
  position: absolute;
  -webkit-transition: all 1s;
  transition: all 1s;
}
.slider__panel .slider__part img {
  width: 200%;
  height: 200%;
  object-fit: cover;
  object-position: center center;
  position: absolute;
}
.slider__panel .slider__part .slider__part-1 {
  top: 0;
  left: 0;
}
.slider__panel .slider__part .slider__part-2 {
  top: 0;
  right: 0;
}
.slider__panel .slider__part .slider__part-3 {
  bottom: 0;
  left: 0;
}
.slider__panel .slider__part .slider__part-4 {
  bottom: 0;
  right: 0;
}
.slider__panel .slider__part.slider__part-1 {
  top: 0;
  left: -100%;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
}
.slider__panel .slider__part.slider__part-2 {
  top: 0;
  right: -100%;
  -webkit-transition-delay: 0.1s;
          transition-delay: 0.1s;
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
}
.slider__panel .slider__part.slider__part-3 {
  bottom: 0;
  left: -100%;
  -webkit-transition-delay: 0.2s;
          transition-delay: 0.2s;
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
}
.slider__panel .slider__part.slider__part-4 {
  bottom: 0;
  right: -100%;
  -webkit-transition-delay: 0.3s;
          transition-delay: 0.3s;
  -webkit-transition-duration: 1s;
          transition-duration: 1s;
}
.slider__panel_current .slider__part.slider__part-1 {
  top: 0;
  left: 0;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  -webkit-transition-duration: 0s;
          transition-duration: 0s;
}
.slider__panel_current .slider__part.slider__part-2 {
  top: 0;
  right: 0;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  -webkit-transition-duration: 0s;
          transition-duration: 0s;
}
.slider__panel_current .slider__part.slider__part-3 {
  bottom: 0;
  left: 0;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  -webkit-transition-duration: 0s;
          transition-duration: 0s;
}
.slider__panel_current .slider__part.slider__part-4 {
  bottom: 0;
  right: 0;
  -webkit-transition-delay: 0s;
          transition-delay: 0s;
  -webkit-transition-duration: 0s;
          transition-duration: 0s;
}

